@import url(https://fonts.googleapis.com/css?family=Allerta+Stencil);
html {
  height: 100%;
}

body {
  height: 100%;
}

#root {
  height: 100%;
  font-family: 'Allerta Stencil', sans-serif;
}

.home {
  padding-top: 250px;
}

.home {
  background: url(/static/media/bg-home.d8566fc0.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  padding-top: 250px;
}

.btn-ghost {
  background: transparent;
}

.btn-ghost.btn-white {
  color: #fff;
  border: 1px solid #fff;
}

.btn-ghost.btn-white:hover {
  color: #3c3c3c;
  background: #fff;
}

img:hover {
  cursor: pointer;
  opacity: 0.5;
  -webkit-transition: 0.7s;
  transition: 0.7s;
}

.gallery-container {
  padding-top: 100px;
} 

.App {
  height: 100%;
}

